<template>
  <div id="question">
    <head-top title="我的提问"></head-top>
    <div class="main" ref="main">
      <div class="questions" v-for="(item, i) in dataList" :key="i">
        <div class="time">{{item['CreateTime'] | formatDate(item['CreateTime']) }}</div>
        <div class="info">{{item['QContent']}}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      dataList: [],
      content: ""
    };
  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      this.getDataList();
    });
  },
  methods: {
    getDataList() {
      this.dataList = [];
      this.$mainHttp.question
        .getList({
          CIID: this.$store.getters.courseID,
          studentID: this.$store.getters.studentID
        })
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.dataList.push(item);
            });
          }
        })
        .catch(err => {});
    }
  },
  computed: {},
  watch: {}
};
</script>

<style lang='less' scoped>
@import url("./../../../styles/common/base.less");
#question {
  width: 100%;
  height: 100%;

  .main {
    // overflow: hidden;
    height: calc(100vh - 45px);
    overflow-y: auto;

    .questions {
      width: 92vw;
      padding: 5px 2vw;
      margin: 0 auto;
      margin-top: 10px;

      text-align: left;
      background: rgba(255, 255, 255, 1);
      border-radius: 3px;

      word-break: break-all;
      word-wrap: break-word;
      .time {
        font-size: @font-info;
        font-family: MicrosoftYaHei;
        color: rgba(102, 102, 102, 1);
        line-height: 2.5;
      }
      .info {
        padding: 5px 0;
        font-size: @font-info;
        line-height: 1.6;
        color: rgba(51, 51, 51, 1);
      }
      &:last-child {
        margin-bottom: 10px;
      }
    }
  }
}
</style>


